<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'App'
  }

</script>

<style>
  * {
    margin: 0;
    padding: 0;
  }

  html,
  body,
  #app {
    font-family: "Helvetica Neue", "Segoe UI", Tahoma, Arial, "Hiragino Sans GB", STHeiti, "Microsoft Yahei", "WenQuanYi Micro Hei", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 14px;
    height: 100%;
  }

  .wyfx-full {
    width: 100%;
    height: 100%;
  }

  .wyfx-fullH {
    height: 100%;
  }

  .wyfx-fullW {
    width: 100%;
  }

  .clearfix:after {
    content: "\200B";
    display: block;
    height: 0;
    clear: both;
  }

  .fl {
    float: left;
  }

  .fr {
    float: right;
  }

  .hide {
    display: none;
