import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/pages/Main'
import Home from '@/pages/Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "*",
      redirect: '/main/home'
    },
    {
      path: '/main',
      name: 'Main',
      component: Main,
      children: [{
        path: 'home',
        name: 'Home',
        component: Home
      }]
    }
  ]
})



