<template>
  <div class="wyfx-full">
    <el-upload class="upload wyfx-panel" ref="upload" :action="this.$rootPath + '/wyfx/file/upload'" :on-success="uploadSuccess"
      :on-remove="uploadDel" :file-list="fileList" :limit="1" :on-exceed="handleExceed">
      <el-button slot="trigger" size="small">上传文件</el-button>
      <el-button type="primary" size="small" @click="begin" :loading="btnLoad">{{btnLoadTxt}}</el-button>
    </el-upload>
    <div class="wyfx-panel" :class="{hide: isHide1}">
      <el-progress :percentage="percentage" class="wyfx-progress"></el-progress>
    </div>
    <div class="wyfx-panel" :class="{hide: isHide2}">
      <el-input type="textarea" :rows="2" v-model="textarea">
      </el-input>
    </div>
    <div class="wyfx-panel" :class="{hide: isHide2}">
      <img :src="require('../assets/imgs/logo.png')" alt="" style="width: 100%">
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        fileList: [],
        isHide1: true,
        isHide2: true,
        percentage: 0,
        clock: '',
        btnLoad: false,
        btnLoadTxt: '开始扫描',
        textarea: "Found CFI Vulnerabilities at: ['0x400580', '0x4008b1', '0x400570', '0x40078a', '0x400888']"
      }
    },
    methods: {
      uploadSuccess(response, file, fileList) {
        this.fileList = fileList;
      },
      uploadDel(file, fileList) {
        this.fileList = fileList;
        this.isHide1 = true;
        this.isHide2 = true;
        this.percentage = 0;
      },
      handleExceed() {
        this.$message({
          message: '只能同时扫描一个文件',
          type: 'warning'
        });
      },
      begin() {
        if (this.fileList.length < 1) {
          this.$message({
            message: '请先完成文件上传',
            type: 'warning'
          });
        } else {
          this.btnLoad = true;
          this.btnLoadTxt = '扫描中';
          this.isHide1 = false;
          this.clock = self.setInterval(this.scanning, 1000);
        }
      },
      scanning() {
        if (this.percentage < 100) {
          this.percentage += 10;
        } else {
          window.clearInterval(this.clock);
          this.btnLoad = false;
          this.btnLoadTxt = '开始扫描';
          this.isHide2 = false;
        }
      }
    },
    mounted() {

    }
  }

</script>

<style>
  .upload {
    width: 400px;
  }

  .wyfx-panel {
    margin-bottom: 20px;
  }

</style>



